import React from "react";
import { Form, Input, Select, Button, message, Radio } from "antd";
import { editRule } from "../../../api/index";
const EditForm = props => {
  const { getFieldDecorator } = props.form;
  const data = props.data;
  let handleEditSubmit = event => {
    event.preventDefault();
    props.form.validateFields(async (err, value) => {
      if (!err) {
        for (const key in value) {
          if (value[key] === undefined) {
            value[key] = "";
          }
        }
        const res = await editRule(data.id, value);
        if (res.status === 0) {
          message.success("");
          props.finished();
          props.form.resetFields();
        } else {
          message.error("" + res.msg);
        }
      }
    });
  };
  return (
    <Form labelCol={{ span: 4 }} labelAlign="left" onSubmit={handleEditSubmit}>
      <Form.Item label="">
        {getFieldDecorator("pid", {
          rules: [{ required: true, message: "" }],
          initialValue: data.pid
        })(
          <Select style={{ width: "60%" }}>
            <Select.Option value={0}></Select.Option>
            {props.packageNode}
          </Select>
        )}
      </Form.Item>
      <Form.Item label="">
        {getFieldDecorator("name", {
          rules: [{ required: true, message: "" }],
          initialValue: data.name.replace(/&nbsp;/g, "")
        })(<Input style={{ width: "60%" }} placeholder="" />)}
      </Form.Item>
      <Form.Item label="">
        {getFieldDecorator("module", { initialValue: data.module })(
          <Input style={{ width: "60%" }} placeholder="" />
        )}
      </Form.Item>
      <Form.Item label="">
        {getFieldDecorator("action", { initialValue: data.action })(
          <Input style={{ width: "60%" }} placeholder="" />
        )}
      </Form.Item>
      <Form.Item label="">
        {getFieldDecorator("href", { initialValue: data.href })(
          <Input style={{ width: "60%" }} placeholder="" />
        )}
      </Form.Item>
      <Form.Item label="key">
        {getFieldDecorator("key", { initialValue: data.router_key })(
          <Input style={{ width: "60%" }} placeholder="key" />
        )}
      </Form.Item>
      <Form.Item label="icon">
        {getFieldDecorator("icon", { initialValue: data.icon })(
          <Input style={{ width: "60%" }} placeholder="icon" />
        )}
      </Form.Item>
      <Form.Item label="">
        {getFieldDecorator("sort", {
          rules: [{ required: true, message: "" }],
          initialValue: data.sort
        })(<Input style={{ width: "60%" }} placeholder="" />)}
      </Form.Item>
      <Form.Item label="">
        {getFieldDecorator("status", {
          initialValue: data.status
        })(
          <Radio.Group>
            <Radio value={1}></Radio>
            <Radio value={0}></Radio>
          </Radio.Group>
        )}
      </Form.Item>

      <Form.Item>
        <Button type="primary" htmlType="submit" className="login-form-button">
          
        </Button>
      </Form.Item>
    </Form>
  );
};

const WrappedEditForm = Form.create()(EditForm);

export default WrappedEditForm;
