import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import { Form, Icon, Input, Button, Modal, message } from "antd";
import "./login.less";
import { reqLogin, reqAuthCode, navList, setToken } from "../../api";

const Item = Form.Item;

/*

 */
class Login extends Component {
  getMenuList = async () => {
    const result = await navList();
    if (result.status === 0) {
      let { data } = result;
      data.forEach(element => {
        if (element.children) {
          element.children.forEach(item => {
            item.title = item.title.slice(24);
          });
        }
      });
      localStorage.menuList = JSON.stringify(data);
      //  ()
      this.props.history.replace("/");

      // setTimeout(() => this.props.history.replace("/"), 300);
    }
  };
  handleSubmit = event => {
    // 
    event.preventDefault();

    // 
    this.props.form.validateFields(async (err, values) => {
      // 
      if (!err) {
        // 
        const { username, password, authcode } = values;
        const result = await reqLogin(username, password, authcode);

        if (result.status === 0) {
          // 
          message.success("");
          localStorage.token = result.data.token;
          localStorage.name = result.data.name;
          localStorage.tokenTimeStamp = new Date().getTime();
          setToken();
          this.getMenuList();
        } else {
          // 
          // 
          message.error(result.msg);
        }
      } else {
        console.log("!");
      }
    });
  };

  handleClick = () => {
    this.props.form.validateFields(async (err, values) => {
      // 
      if (!err) {
        // 
        const { username, password } = values;
        const result = await reqAuthCode(username, password);
        // console.log("", result);
        if (result.status === 0 && result.data) {
          // 
          Modal.info({
            title: "",
            content: (
              <div>
                <img src={result.data.qrurl} alt="" />
              </div>
            )
          });
        } else {
          // 
          // 
          message.error(result.msg);
        }
      } else {
        console.log("!");
      }
    });

    // form
    // const form = this.props.form
    // // 
    // const values = form.getFieldsValue()
    // console.log('handleSubmit()', values)
  };
  /*
  
  */
  /*
   /
     1). 
     2). 4
     3). 12
     4). 
    */
  validatePwd = (rule, value, callback) => {
    console.log("validatePwd()", rule, value);
    if (!value) {
      callback("");
    } else if (value.length < 4) {
      callback("4");
    } else if (value.length > 12) {
      callback("12");
    } else if (!/^[a-zA-Z0-9_]+$/.test(value)) {
      callback("");
    } else {
      callback(); // 
    }
    // callback('xxxx') // , 
  };

  render() {
    // , 
    const token = localStorage.token;
    if (token) {
      return <Redirect to="/" />;
    }
    // form
    const form = this.props.form;
    const { getFieldDecorator } = form;

    return (
      <div className="login">
        <header className="login-header">
          <h1>QGame: </h1>
        </header>
        <section className="login-content">
          <h2></h2>
          <Form onSubmit={this.handleSubmit} className="login-form">
            <Item>
              {/*
              /
                1). 
                2). 4
                3). 12
                4). 
               */}
              {getFieldDecorator("username", {
                // : 
                // : 
                rules: [
                  {
                    required: true,
                    whitespace: true,
                    message: ""
                  },
                  { min: 3, message: "3" },
                  { max: 12, message: "12" },
                  {
                    pattern: /^[a-zA-Z0-9_]+$/,
                    message: ""
                  }
                ]
              })(
                <Input
                  prefix={
                    <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder=""
                />
              )}
            </Item>
            <Form.Item>
              {getFieldDecorator("password", {
                rules: [
                  {
                    validator: this.validatePwd
                  }
                ]
              })(
                <Input
                  prefix={
                    <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  type="password"
                  placeholder=""
                />
              )}
            </Form.Item>
            <Form.Item>
              {getFieldDecorator("authcode", {
                rules: [
                  //{ required: true, whitespace: true, message: '' },
                  //{ min: 4, message: '4' }
                ]
              })(
                <Input
                  prefix={
                    <Icon type="qrcode" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  type="password"
                  placeholder=""
                />
              )}
            </Form.Item>
            <span style={{ marginTop: 5 }} onClick={this.handleClick}>
              Google Authenticator
              
            </span>
            <Form.Item>
              <Button
                type="primary"
                htmlType="submit"
                className="login-form-button"
              >
                
              </Button>
            </Form.Item>
          </Form>
        </section>
      </div>
    );
  }
}
const WrapLogin = Form.create()(Login);
export default WrapLogin;
