import React, { Component } from "react";
import {
  Card,
  Table,
  Modal,
  message,
  Icon,
  Input,
  Popconfirm,
  Select,
  Button,
  Form
} from "antd";
import { ruleList, addRule, ruleDel } from "../../../api/index";
import LinkButton from "../../../components/link-button";
import WrappedEditForm from "./edit";

class Rule extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      count: 0,
      isEditFormShow: false
    };
  }
  getInitialData = async () => {
    const res = await ruleList();
    if (res.status === 0) {
      this.setState({
        data: res.data,
        count: res.count
      });
    } else {
      message.error(res.msg);
    }
  };
  componentDidMount() {
    this.getInitialData();
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    const packageNode = this.state.data.map(item => {
      return (
        <Select.Option value={item.id} key={item.name}>
          {item.name.replace(/&nbsp;/g, "-")}
        </Select.Option>
      );
    });
    return (
      <Card
        title={
          <div>
            <Form layout="inline" onSubmit={this.handleSubmit}>
              <Form.Item>
                {getFieldDecorator("pid", {
                  rules: [{ required: true, message: "" }],
                  initialValue: "0"
                })(
                  <Select style={{ width: 200 }}>
                    <Select.Option value="0"></Select.Option>
                    {packageNode}
                  </Select>
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator("name", {
                  rules: [{ required: true, message: "" }]
                })(<Input style={{ width: 120 }} placeholder="" />)}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator("module", {})(
                  <Input style={{ width: 120 }} placeholder="" />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator("action", {})(
                  <Input style={{ width: 120 }} placeholder="" />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator("href", {})(
                  <Input style={{ width: 120 }} placeholder="" />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator("key", {})(
                  <Input style={{ width: 120 }} placeholder="key" />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator("icon", {})(
                  <Input style={{ width: 120 }} placeholder="icon" />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator("sort", {
                  rules: [{ required: true, message: "" }]
                })(<Input style={{ width: 120 }} placeholder="" />)}
              </Form.Item>
              <Form.Item>
                <Button
                 size='default' 
                  type="primary"
                  htmlType="submit"
                  className="login-form-button"
                >
                  
                </Button>
              </Form.Item>
            </Form>
          </div>
        }
        extra={
          <LinkButton onClick={() => window.location.reload()} size='default'>
            <Icon type="reload" />
          </LinkButton>
        }
      >
        <Table
          bordered
          rowKey={(record, index) => `${index}`}
          dataSource={this.state.data}
          columns={this.initColumns()}
          size="small"
          pagination={{
            defaultPageSize: 20,
            showSizeChanger: true,
            showQuickJumper: true,
            showTotal: (total, range) => `${total}`,
            defaultCurrent: 1,
            total: this.state.count,
            onChange: (page, pageSize) => {
              this.getInitialData(page, pageSize);
            },
            onShowSizeChange: (current, size) => {
              this.getInitialData(current, size);
            }
          }}
        />
        <Modal
          title=""
          visible={this.state.isEditFormShow}
          onCancel={() => {
            this.setState({ isEditFormShow: false });
          }}
          footer={null}
        >
          <WrappedEditForm
            finished={() => {
              this.getInitialData();
              this.setState({ isEditFormShow: false });
            }}
            packageNode={packageNode}
            data={this.editDataRecord}
          />
        </Modal>
      </Card>
    );
  }
  initColumns = () => [
    {
      title: "",
      dataIndex: "name",
      render: (text, record, index) => (
        <span>{record.name.replace(/&nbsp;/g, "-")}</span>
      )
    },
    {
      title: "",
      dataIndex: "module"
    },
    {
      title: "",
      dataIndex: "action"
    },
    {
      title: "",
      dataIndex: "href"
    },
    {
      title: "key",
      dataIndex: "router_key"
    },
    {
      title: "icon",
      dataIndex: "icon"
    },
    {
      title: "",
      dataIndex: "status"
    },
    {
      title: "",
      dataIndex: "sort"
    },
    {
      title: "",
      dataIndex: "",
      render: (text, record, index) => (
        <span>
          <Button type="primary" onClick={() => this.edit(record)} size="small">
            
          </Button>
          &nbsp;&nbsp;&nbsp;
          <Popconfirm
            title="?"
            onConfirm={() => this.onDelete(record)}
            okText=""
            cancelText=""
          >
            <Button type="danger" size="small">
              
            </Button>
          </Popconfirm>
        </span>
      )
    }
  ];
  handleSubmit = event => {
    event.preventDefault();
    this.props.form.validateFields(async (err, value) => {
      if (!err) {
        for (const key in value) {
          if (value[key] === undefined) {
            value[key] = "";
          }
        }
        const res = await addRule(value);
        if (res.status === 0) {
          message.success("");
          this.getInitialData();
          this.props.form.resetFields();
        } else {
          message.error("" + res.msg);
        }
      }
    });
  };
  onDelete = async record => {
    let res = await ruleDel(record.id);
    if (res.status === 0) {
      message.success("");
      this.getInitialData();
    } else {
      message.error("" + res.msg);
    }
  };
  edit = async record => {
    this.editDataRecord = record;
    this.setState({
      isEditFormShow: true
    });
  };
}
const RuleManage = Form.create()(Rule);
export default RuleManage;
